export default {
	data() {
		return {
			num: 60,
			show: true
		}
	},
	methods: {
		codes(){
			console.log(11)
			this.show =! this.show
			if(!this.show){
				var a = setInterval(()=>{
					this.num--
					if(this.num<0){
						clearInterval(a)
						this.show = true
						this.num = 60
					}

				},1000)
				
			}
		}
	}
}
